<script>
import api from '@/api'
import { getToken,delToken } from '@/api/auth'

export default {
	data () {
		const validatePass = (rule, value, callback) => {
			const reg = !value.trim().match(/^[a-zA-Z]{1,}\d{1,}$/) || value.trim().length < 6;
			if (value === '') {
				callback(new Error('请输入新密码'));
			} else if (reg) {
        callback(new Error('请输入以字母开头的不少于六位的数字字母组合密码'));
      } else {
				if (this.formCustom.passwdCheck !== '') {
						// 对第二个密码框单独验证
						this.$refs.formCustom.validateField('passwdCheck');
				}
				callback();
			}
		};
		const validatePassCheck = (rule, value, callback) => {
				if (value === '') {
						callback(new Error('请确认新密码'));
				} else if (value !== this.formCustom.passwd) {
						callback(new Error('两次密码设置不相同!'));
				} else {
						callback();
				}
		};
		const validateOldPass = (rule, value, callback) => {
				if (!value) {
						return callback(new Error('请输入原密码'));
				} else if (value == this.formCustom.passwd) {
						callback(new Error('新老密码重复!'));
				} else {
						callback();
				}
		};
		
		return {
				formCustom: {
						passwd: '',
						passwdCheck: '',
						oldpasswd: ''
				},
				ruleCustom: {
						passwd: [
								{ required: true, validator: validatePass, trigger: 'blur' }
						],
						passwdCheck: [
								{ required: true, validator: validatePassCheck, trigger: 'blur' }
						],
						oldpasswd: [
								{ required: true, validator: validateOldPass, trigger: 'blur' }
						]
				}
		}
	},
	methods: {
			handleSubmit (name) {
				this.$refs[name].validate((valid) => {
					if (valid) {
						let token = getToken(),
								username = this.$store.state.userInfo.username,
								oldpasswd = this.formCustom.oldpasswd,
								newpasswd1 = this.formCustom.passwd,
								newpasswd2 = this.formCustom.passwdCheck,
								params = {"token": token, "username": username, "oldpasswd": oldpasswd, "newpasswd1": newpasswd1, "newpasswd2": newpasswd2};
						
						api.changePass(params).then(res => {
							if(res.code === 0) {
								delToken();
								this.$Notice.success({title:'密码重置成功!'});
								this.$router.push({path:'/login'})
							} else {
								this.$Notice.error({title:res.msg});
							}
						})
							
					} else {
							// this.$Notice.error({title:'请按规定填写密码!'});
					}
				})
			},
			handleReset (name) {
				this.$refs[name].resetFields();
			}
	}
}
</script>
<template>
<div>
	<h3><Icon type="md-compass" size=24 />修改密码</h3>
	<Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="100">
    <FormItem label="原密码" prop="oldpasswd">
      <Input type="password" v-model="formCustom.oldpasswd"></Input>
    </FormItem>
    <FormItem label="新密码" prop="passwd">
      <Input type="password" v-model="formCustom.passwd"></Input>
    </FormItem>
    <FormItem label="确认新密码" prop="passwdCheck">
      <Input type="password" v-model="formCustom.passwdCheck"></Input>
    </FormItem>
    <FormItem>
      <Button type="primary" @click="handleSubmit('formCustom')">提交</Button>
      <Button @click="handleReset('formCustom')" style="margin-left: 8px">重置</Button>
    </FormItem>
  </Form>
</div>
</template>
<style lang="scss" scoped>
	.ivu-form {
		width: 28%;
	}
</style>